  .my-profile-overlay {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0, 0, 0, 0.5);
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 1000;
  }
  
  .myprofile-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    font-size: 18px;
    font-weight: bold;
    margin-bottom: 20px;
  }
  
  .my-profile-content {
    background-color: white;
    padding: 20px;
    border-radius: 8px;
    width: 400px;
    max-width: 90%;
    position: relative;
  }
  
  .my-profile-close-button {
    position: absolute;
    top: 10px;
    right: 10px;
    background: none;
    border-radius: 50%;
    font-size: 18px;
    cursor: pointer;
  }
  
  .my-profile-details {
    margin-top: 20px;
  }
  
  .my-profile-detail-row {
    display: flex;
    justify-content: space-between;
    margin-bottom: 10px;
  }
  
  .my-profile-detail-row label {
    font-weight: bold;
  }
  
  .my-profile-detail-row span {
    font-size: 16px;
  }
  